import uniStatusBar from '@/components/uni-status-bar/uni-status-bar.vue';
import otherLogin from '@/components/home/otherLogin.vue';
export default {
	components: {
		uniStatusBar,
		otherLogin
	},
	data() {
		return {
			disable: true,
			loading: false,
			status:false,//false代表账号密码登录，true代表手机验证码登录
			username:'',
			password:'',
			phone:'',
			checknum:'',
			codetime:0,
			providerList:[]
		};
	},
	watch:{
		username(val){
			this.OnBtnChange();
		},
		password(val){
			this.OnBtnChange();
		},
		phone(val){
			this.OnBtnChange();
		},
		checknum(val){
			this.OnBtnChange();
		},
	},
	computed:{
		statusName(){
			return this.status?"手机验证码登录":"账号密码登录"		
		}
	},
	onLoad() {
		uni.getProvider({
			service: 'oauth',
			success: (result) => {
				console.log('拿到支持的登录',result)
				this.providerList = result.provider.map((value) => {
					let providerName = '';
					let icon="";
					switch (value) {
						case 'weixin':
							providerName = '微信登录';
							icon='weixin';
							break;
						case 'qq':
							providerName = 'QQ登录';
							icon='QQ'
							break;
						case 'sinaweibo':
							providerName = '新浪微博登录';
							icon='xinlangweibo'
							break;
					}
					return {
						name: providerName,
						icon:icon,
						id: value
					}
				});
		
			},
			fail: (error) => {
				console.log('获取登录通道失败', error);
			}
		});
	},
	methods: {
		//返回上一步
		back() {
			console.log('返回上一步');
			uni.navigateBack({
				delta:1
			})
		},
		//提交登录
		submit() {
			if(this.status){
				//手机验证码登录
				//验证手机号合法性
				if(!this.isPhone()){
					uni.showToast({
						title:"请输入正确手机号",
						icon:"none"
					})
				}
			}else{
				//账号密码登录
			}
		},
		//切换登录状态
		changeStatus(){
			this.initInput();
			this.status=!this.status;
		},
		//改变按钮状态
		OnBtnChange(){
			if((this.username&&this.password)||(this.phone&&this.checknum)){
				this.disable=false;
			}else{
				this.disable=true;
			}
		},
		//初始化表单
		initInput(){
			this.username=""
			this.password="";
			this.phone="";
			this.checknum="";
		},
		//获取验证码
		getCheckNum(){
			if(this.codetime>0){
				uni.showToast({
					title:"不能重复获取验证码",
					icon:"none"
				})
				return
			}
			//验证手机号合法性
			if(!this.isPhone()){
				uni.showToast({
					title:"请输入正确手机号",
					icon:"none"
				})
			}
			//请求服务器，发送验证码
			//发送成功，开启倒计时
			this.codetime=10;
			let timer=setInterval(()=>{
				this.codetime--;
				if(this.codetime<1){
					clearInterval(timer);
					this.codetime=0;
				}
			},1000)
			
		},
		isPhone(){
			//验证手机号合法性
			let mPattern=/^1[34578]\d{9}$/;
			return mPattern.test(this.phone)
		}
	}
};
